body {
  background-color: #f4eade;
}
.App {
  text-align: center;
  flex-direction: row;
  display: flex;
  height: 100%;
}
.content {
  font-size: calc(10px + 2vmin);
  color: #303030;
}
.title {
  color: #5F5E5E;
}
.left {
  float: 'left';
  flex: 5;
  margin-left: 5%;
}
.right {
  float: 'right';
  flex: 1;
  /* margin-right: 5%; */
  padding: 2.5vw;
}
.lists {
  display: flex;
  flex-direction: row-reverse;
  padding-right: 10%;
  margin-top: 5%;
}
.list {
  list-style-type:none;
  flex-direction: row;
  text-align: center;
}
.tagline {
  font-size: calc(10px + 3vmin);
  color: #c9c4bf;
}
a {
  color: #3D9BDA;
}
.papers-list {
  font-size: calc(10px + 1.5vmin);
}